.contact-us {
  @include grid-adaptive;
  @include padding-bottom-contact-us;

  &__title {
    @include grid-column;

    line-height: 100%;
  }

  &__form {
    margin: 48px 0;

    @include grid-column;

    @include on-tablet {
      grid-column: span 3;
    }

    @include on-desktop {
      grid-column: span 5;
    }
  }

  &__input {
    height: 48px;
    margin-bottom: 16px;
    padding: 14px 16px;
    background-color: #f8f8fa;
    border-radius: 40px;

    @include input-width;

    &::placeholder {
      font-size: 14px;
      line-height: 20px;
      letter-spacing: 0;
      font-weight: 400;
      color: #7e7e83;
    }

    border: 1px solid transparent;

    @include hover(border-color, #d5d7de);
    @include focus(border-color, #292929);
  }

  &__button {
    @include on-tablet {
      &:hover {
        cursor: pointer;
        box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.4);
      }
      &:active {
        box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.64);
      }
    }

    @include on-desktop {
      width: 411px;
    }
  }

  &__info {
    @include grid-column-move-free-contact-us;

    @include on-desktop {
      grid-column: 8 / -1;
    }
  }
}

textarea.contact-us__input {
  height: 148px;
  width: 100%;
  border-radius: 16px;
  margin-bottom: 32px;
  padding: 16px;
  scroll-margin-top: 50px;
}

.info {
  & > * {
    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }

  @include on-tablet {
    margin: 48px 0;
  }

  & > * > span {
    font-size: 14px;
    font-weight: 400;
    margin-bottom: 8px;
    line-height: 140%;
  }

  & > * > a {
    &:hover {
      color: $hover-text-color;
    }
  }

  &__phone {
    @include grid-column;
  }

  &__email {
    @include grid-column;
  }

  &__address {
    @include grid-column;
  }
}
